import React, { useEffect } from 'react';
import { Navigate, NavLink, useParams } from 'react-router-dom';
import clNames from './Dialogs.module.css';
import { Field, reduxForm } from 'redux-form';
import { MaxLengthCreator, requiredField } from '../../utils/validators';
import { MessagesTextarea, Textarea } from '../FormsControls';
import Button from '../common/Button/Button';
import { useState } from 'react';
import { messagesAPI } from '../../api/api';
import { Avatar, AvatarMini } from '../common/Avatar/Avatar';
import Preloader from '../Preloader/Preloader';
import circleImg from '../../newMessageIcon.png';

const MaxLength500 = MaxLengthCreator(500);

const Dialog = (props) => {
    let [messageState, setState] = useState('unread');
    let lastMessage = null;
    if (props.messages) { 
        lastMessage = props.messages[props.messages.length - 1].text;
    }
    return (
        <NavLink to={'/dialogs/' + props.id} onClick={()=> setState('read')}>
            <div className={clNames.dialogContainer + ' ' + (messageState === 'unread' ? clNames.unread : clNames.read)}>
            <Avatar avatarSrc={props.avatar} />
            <div className={clNames.incomingMessageInfo}>{props.name}<div className={clNames.lastMessageCntr}>{lastMessage}</div></div>
            {messageState === 'unread' && <div><img src={circleImg} className={clNames.icon}/></div>}
            </div></NavLink>
    )
};

const DialogsSidebar = (props) => {
    let state = props.state;
    let dialogsElements = state.dialogsData.map(dialog => <Dialog name={dialog.name} id={dialog.id} avatar={dialog.avatar} messages={state.messagesData.filter(message => +message.userId === dialog.id)}/>);
    return <div className={clNames.dialogsSidebar} onClick={() => props.openDialogWindow(true)}>
        {dialogsElements}
    </div>
};

const Message = (props) => {
    let destination = props.destination;
    return (
        <span className={clNames.message + ' ' + (destination === 'in' ? clNames.in : clNames.out)}>{props.text}</span>
    )
};

const DialogForm = (props) => {
    return <form onSubmit={props.handleSubmit} className={clNames.dialogForm}>
        <Field component={MessagesTextarea} placeholder='Write a message...' value={props.newMessageText} name="newMessageText" validate={[MaxLength500]} />
        <Button content="Send" />
    </form>
};

const DialogReduxForm = reduxForm({
    // a unique name for the form
    form: 'messageText'
})(DialogForm)

const DialogWindow = (props) => {
    let { userId } = useParams();
    useEffect(() => {
        let messages = messagesAPI.getDialogMessages(userId);
        props.setOpenedDialog(messages);
    }, [userId]);
    if (!props.state.openedDialogMessages) {
        return <Preloader/>
    }
    let selectedDialog = props.state.dialogsData.filter(dialog => dialog.id === +userId);
    let messagesElements = props.state.openedDialogMessages.map(message => <Message text={message.text} id={message.id} destination={message.destination} />);
    const onSubmit = (formData) => {
        props.addMessage(formData.newMessageText);
    }
    return <div className={clNames.dialogWindow}>
        <div className={clNames.dialogHeader}>
            <AvatarMini avatarSrc={selectedDialog[0].avatar} />
            <div className={clNames.name}>{selectedDialog[0].name}
                <div className={clNames.onlineStatus}>Was online today</div>
            </div>
        </div>
        <div className={clNames.messagesContainer}>{messagesElements}</div>
        <DialogReduxForm onSubmit={onSubmit} />
    </div>
}

const Dialogs = (props) => {
    debugger
    let [isDialogSelected, setIsDialogSelected] = useState(false);
    useEffect(() => {
        let messages = messagesAPI.getAllMessages();
        props.setDialogsPage(messages);
    }, []);
    if (!props.dialogsPage.messagesData) {
        return <div>
            <Preloader />
        </div>
    };
    let state = props.dialogsPage;
    if (!props.isAuth) return <Navigate to={"/login"} />
    return (
        <div className={clNames.dialogs}>
            <DialogsSidebar state={state} openDialogWindow={setIsDialogSelected} />
            <div className={clNames.messagesArea}>
                {isDialogSelected
                    ? <DialogWindow state={state} addMessage={props.addMessage} setOpenedDialog={props.setOpenedDialog}/>
                    : <span>Select a dialogue to start chatting.</span>
                }
            </div>
        </div>
    )
};


export default Dialogs;