const ADD_MESSAGE = 'dialogs/ADD-MESSAGE';
const SET_DIALOG_MESSAGES = 'dialogs/GET_DIALOG_MESSAGES';
const SET_ALL_MESSAGES = 'dialogs/SET-ALL-MESSAGES';

let initialState = {
    dialogsData: [{ id: 1, name: "Legolas", avatar: 'http://i.absurdopedia.net/thumb/2/28/%D0%9B%D0%B5%D0%B3%D0%BE%D0%BB%D0%B0%D1%81.jpg/300px-%D0%9B%D0%B5%D0%B3%D0%BE%D0%BB%D0%B0%D1%81.jpg' },
    { id: 2, name: "Kisuke Urahara", avatar: 'https://shikimori.one/system/characters/original/210.jpg' }],
    messagesData: null,
    openedDialogMessages: null,
}

const dialogsReducer = function (state = initialState, action) {
    //     ,  -   state
    switch (action.type) {
        case ADD_MESSAGE: {
            debugger
            let newMessage = {
                id: 3,
                text: action.newMessageText,
                destination: 'out'
            };
            return {
                ...state,
                openedDialogMessages: [...state.openedDialogMessages, newMessage],
                newMessageText: '',
            };
        }
        case SET_DIALOG_MESSAGES: {
            debugger
            return {
                ...state,
                openedDialogMessages: action.messages
            }
        }
        case SET_ALL_MESSAGES: {
            debugger
            return {
                ...state,
                messagesData: action.messages
            }
        }
        default:
            return state;
    };
};

export default dialogsReducer;

export const addMessageActionCreator = (newMessageText) => ({ type: ADD_MESSAGE, newMessageText });
export const getDialogMessagesSuccess = (messages) => ({ type: SET_DIALOG_MESSAGES, messages });
export const getAllMessagesSucces = (messages) => ({type: SET_ALL_MESSAGES, messages});