import React from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { AuthRedirect } from '../../hoc/AuthRedirect';
import { addMessageActionCreator, getAllMessagesSucces, getDialogMessagesSuccess } from '../../redux/dialogs-reducer';
import Dialogs from './Dialogs';

let mapStateToProps = function(state) {
    return {
        dialogsPage: state.dialogsPage,
    }
}
// , .     
let mapDispatchToProps = function(dispatch) {
    return {
        addMessage: (newMessageText) => {dispatch(addMessageActionCreator(newMessageText));},
        setOpenedDialog: (messages) => {dispatch(getDialogMessagesSuccess(messages))},
        setDialogsPage: (messages) => {dispatch(getAllMessagesSucces(messages))}
    }
}



//connect   ,    ,   , 
//   
//connect    state,    

export default compose(connect(mapStateToProps, mapDispatchToProps), AuthRedirect)(Dialogs);